	module.exports = {"data":{"markdownRemark":{"html":"<h2>What Is the Domain of a Function</h2>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<p>The domain of a function is the complete set of possible values of the independent variable. In other words, the domain is the set of all possible x-values which will make the function “work”, and will output real y-values.</p>\n<p>For example, for <span class='texhtml'>y = sqrt(x + 2)</span>, the domain of this function is <span class='texhtml'>x >= -2</span>.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href='https://www.intmath.com/functions-and-graphs/2a-domain-and-range.php' target='_blank' rel='nofollow'>Domain and Range of a Function</a></p>","fields":{"slug":"/mathematics/what-is-the-domain-of-a-function/"},"frontmatter":{"title":"What Is the Domain of a Function"}}},"pathContext":{"slug":"/mathematics/what-is-the-domain-of-a-function/"}}

